import { Component } from '@angular/core';
import { App, IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { ImagePicker } from '@ionic-native/image-picker';

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {

  profileImages: any[];

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public imagePicker: ImagePicker,
    public app: App ) {
      this.profileImages = [
        'assets/img/instagram/insta_0.jpg',
        'assets/img/instagram/insta_1.jpg',
        'assets/img/instagram/insta_2.jpg',
        ''
      ]
  }

  ionViewDidLoad() {
  }

  close() {
    this.viewCtrl.dismiss();
  }

  openPhotoPicker(index) {
    this.imagePicker.getPictures({ maximumImagesCount: 1 })
      .then((results) => {
        this.profileImages[index] = results[0];
      }, (err) => { });
  }

  removeImage(index) {
    this.profileImages[index] = '';
  }

  goTo(page, direction) {
    this.navCtrl.push(page, {}, {
      direction: direction
    });
  }

}



